<div class="div privacy-policy"><h1>{{$t('privacy.title')}}</h1><p>歡迎您使用我們的服務！我們非常重視您的隱私權保護，特此說明自學2.0(以下簡稱本平台)的互助旗內容是完全公開的。程式並會需要您的Email以登錄，請選擇您方便公開的Email。</p><P>使用本服務時，請您謹慎考慮哪些資訊是您願意公開於網路上。我們鼓勵您只分享您願意讓他人知道的資訊。</P><P> <b>{{$t('privacy.strong')}}</b></P><p>為了您的資料安全，本平台提供了資料下載備份的選項。您可以隨時下載您的互助旗資料，以便於自行管理和備份。您也可以在登入後刪除您自己的旗幟。</p><h2>創用授權</h2><p><img src="../assets/Cc_by-nd_icon.svg.png" alt="CC-BY-ND Icon"></p><p>您於本平台上的互助旗，均以創用授權CC-BY-ND公開於網路。這意味著您授權他人自由分享和傳播您的內容，但不得對其進行修改，並且必須給予適當的署名。</p><h2>CC-BY-ND授權條款的詳細說明</h2><p>CC-BY-ND（署名-禁止演繹）是一種創作共用授權條款，允許他人以任何形式和任何目的再利用您的作品，但有兩個條件：</p><ul><li><strong>署名（BY）：</strong>使用您的作品時，必須給予您適當的署名，提供作品的名稱，並指出是否對作品進行了修改。具體的署名方式可以由您自行決定。</li><li><strong>禁止演繹（ND）：</strong>使用者不能對您的作品進行任何修改，改編或以其他方式創作衍生作品。</li></ul><p>這意味著他人可以自由分享您的作品，但必須保持作品的完整性，並且必須按照您指定的方式進行署名。</p><h2>數據安全和下載備份</h2><p>我們提供資料下載備份的選項，讓您可以隨時下載並管理您的互助旗資料。請定期下載備份，以確保您的資料安全。</p><h2>管理員權利</h2><p>若遇到特殊情況，例如用戶兩年以及未更新旗幟或是出現明顯冒犯性語言等問題，自學2.0管理員保有移除互助旗之權利，但管理員仍需遵守創用授權，不得局部修改用戶之互助旗。</p><h2>聯絡我們</h2><p>如果您對於隱私權政策有任何疑問或建議，請隨時聯絡我們。管理員聯絡方式如下：</p><ul><li>電子郵件：<a href="mailto:bestian@gmail.com">bestian@gmail.com</a></li></ul><button @click="downloadBackup" v-if="user &amp;&amp; user.uid">下載我的備份</button><button @click="loginGoogle" v-else><i class="google icon"></i>登入以下載備份</button></div>